import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/pages/Home';
import Timeline from '@/pages/Timeline';
import News from '@/pages/News';
import Article from '@/pages/Article';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/news',
      name: 'news',
      component: News,
      // children:{
      //   name:'newsId',
      //   path:'',
      //   component:
      // }
    },
    {
      path: '/news/:id',
      name: 'newsID',
      component: Article,
    },
    {
      path: '/timeline',
      name: 'timeline',
      component: Timeline,
    },
  ],
});



