<template>
<div class="container">
  <el-row>
    <el-col :md="12">
        <div class="amap-wrapper">
        <el-amap ref="map" vid="amapDemo" :center="center" :zoom="zoom"  :scrollWheel="scrollWheel" :plugin="plugin" class="amap-demo">
            <el-amap-marker vid="component-marker" :visible="componentMarker.visible" :position="componentMarker.position" :content="componentMarker.content" ></el-amap-marker>
      </el-amap>
    </div>
    </el-col>
    <el-col :md="10" style="">
        <h3>联系我们</h3>
        <div>
            <ul>
            <li>海南大学植物保护学院 </li>
            <li>网址：http://www.hainanu.edu.cn/nonglin/</li>
            <li>地址：海南省海口市人民大道58号海南大学 </li>
            </ul>
        </div>
    </el-col>
    <el-col :md="2">
      <img src="../assets/wx3.png" alt="" style="margin-top: 70px">
    </el-col>
</el-row>
</div>
</template>

<script>
export default {
  data() {
    return {
      center: [110.328801, 20.056749],
      scrollWheel: false,
      zoom: 16,
      componentMarker: {
        position: [110.328801, 20.056749],
        // content: 'hello!',
      },
      plugin: ['ToolBar', {
        pName: 'MapType',
        defaultType: 0,
      }],
    };
  },
};
</script>

<style lang='less' scoped>
@import url('../styles/common.less');
.amap-wrapper {
    padding: 10px;
    height: 200px;
}
</style>



