<template>
  <div class="container underNav">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/news">新闻</a></el-breadcrumb-item>
      <el-breadcrumb-item>活动详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-main>
      <el-row>
        <el-col :span="24" style="text-align: center">
          <h2>test news</h2>
        </el-col>
      </el-row>
    </el-main>

    <el-card class="box-card" style="width: 1200px">
      <div v-for="module in modules" :key="o" class="text item">
        <span>{{module.title}}</span>
        <div v-for="info in module.news">
          <li>
            <span style="float: right;">info.date</span>
            <a v-bind:href=info.url>{{info.title}}</a>
          </li>
        </div>

      </div>
    </el-card>


    <!--<div class="img-container">-->
    <!--<viewer :images="imgs">-->
    <!--<img  v-for="img in imgs" :src="`https://xieranmaya.github.io/images/cats/${img.url}`">-->
    <!--</viewer>-->
    <!--</div>-->

  </div>

</template>

<script>
  export default {
    data() {
      return {
        newsInfo: [],
        modules: [
          {
            title: '学院新闻',
            news: this.newsInfo,
          },
          {
            title: '通知公告',
            news: this.newsInfo,
          },
          {
            title: '公示栏',
            news: this.newsInfo,
          },
          {
            title: '公示栏',
            news: this.newsInfo,
          },
          {
            title: '公示栏',
            news: this.newsInfo,
          },
          {
            title: '公示栏',
            news: this.newsInfo,
          },
        ],
      };
    },
    mounted() {
      this.axios.get('http://localhost:8080/CardList/getList').then((response) => {
        this.newsInfo = response.data;
      });
    },
  };
</script>


<style lang='less'>

  @import url('../styles/common.less');

  div.underNav{
    margin-top: 80px;
  }
  div.img-container > div{
    padding-top: 60px;
    display: flex;
    flex-wrap: wrap;
    img{
      height: 200px;
      flex-grow: 1;
      object-fit: cover;
      margin: 2px;
      &:hover{
        transform: scale(1.1);
        cursor:pointer;
        box-shadow: 0 0 50px 5px #333;
        transition: all 0.25s ease 0s;
      }
    }
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 480px;
  }

</style>



