<template>
<div class="container" style="padding-top:50px;padding-bottom:50px;">
  <el-row>
    <el-col :md="24">
        <h2>关于我们</h2>
        <p>海南大学植物保护学院（海南大学植物保护学院（Institute of Tropical Agriculture and Forestry, Hainan University）是在省委省政府的精心部署下于2016年7月9日挂牌成立。新成立的植物保护学院由原海南大学农学院、园艺园林学院、环境与植物保护学院和应用科技学院（儋州校区）等学院组建而成，学院最早办学始于1958年的热带作物栽培系，办学历史悠久，办学成果显著，为祖国培养了大批农业科技和生命科学领域优秀人才。</p>
    </el-col>
    <!--<el-col :md="12">-->
        <!--<h2>我们一般做什么？</h2>-->
        <!--<p>这里是我们科协最新的动态资讯和活动公告.</p>-->

            <!--<li v-for="news in all_news">-->
            <!--<a :href="news.url" >{{news.title}}</a>-->
            <!--</li>-->
    <!--</el-col>-->
</el-row>
</div>
</template>

<script>
export default {
  data() {
    return {
      all_news: [
      ],
    };
  },
  mounted() {
    this.axios.get('api/news/').then((response) => {
      this.all_news = response.data;
    });
  },
};
</script>

<style lang='less'>
@import url('../styles/common.less');

.news{
    list-style-type : None;
    a{
        text-decoration:none;
        color: auto;
    }
}
</style>



